import { Component } from '@angular/core';
import { ToastController } from '@ionic/angular';
import { AlertController } from '@ionic/angular';
import { GroceriesServiceService } from '../groceries-service.service';
import { InputDialogService } from '../input-dialog.service';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';

@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page {

  title = "Grocery List";

  constructor(public toastController: ToastController, public socialSharing: SocialSharing, public inputDialogService: InputDialogService, public dataService: GroceriesServiceService, public alertController: AlertController) {

  }

  loadItems() {
    return this.dataService.getItems();
  }

  async removeItem(item, index) {
    const toast = await this.toastController.create({
      message: 'Removing item - ' + index + ' ... ',
      duration: 2000
    });
    toast.present();
    this.dataService.removeItem(index);
  }

  async shareItem(item, index) {
    const toast = await this.toastController.create({
      message: 'Sharing item - ' + index + ' ... ',
      duration: 2000
    });
    toast.present();

    let message = "Grocery Item - Name: " + item.name + " - Quantity: " + item.quantity;
    let subject = "Shared via Groceries App";
    this.socialSharing.share(message, subject).then(() => {
      // Sharing via email is possible
      console.log("Shared Successfully!")
    }).catch((error) => {
      // Sharing via email is not possible
      console.log("Error while sharing", error);
    });
  }

  async editItem(item, index) {
    const toast = await this.toastController.create({
      message: 'Editing item - ' + index + ' ... ',
      duration: 2000
    });
    toast.present();
    this.inputDialogService.showPrompt(item, index);
  }

  addItem(item) {
    this.inputDialogService.showPrompt();
  }
  
}
